import {v4 as guid} from 'uuid';

export type Note = {
    note: string,
    ts: string,
    id: string,
    created_at: number;
    updated_at: number;
    serial: number;
}

function formattedTimeStamp() {
    const d = new Date().toJSON();
    const [day, timeString] = d.split('T');
    const time = timeString.split('.')[0];
    return `${day.replace(/\-/g, '.')} ${time}`;
}

export default function createStamp(note: string, serial: number): Note {
    const dts: number = Date.now();
    return {note, ts: formattedTimeStamp(), id: guid(), created_at: dts, updated_at: dts, serial};
}
