import {append, get, remove, unshift} from "../../lib/localStorge.utils";

export default function useLocalList(key: string) {
    function push(value: string) {
        if (value && value.length >= 3) {
            append<string>(key, value);
        }
    }

    function addAtBeginning(value: string) {
        if (value && value.length > 0) {
            unshift<string>(key, value);
        }
    }

    function fetch(): string[] {
        return JSON.parse(get(key) as string) || [] as string[];
    }

    function clear() {
        remove(key);
    }

    return {push, fetch, clear, addAtBeginning};
}
