import React from "react";

export default function AddText(props: any) {
    const {btnActions, inputActions, value} = props;
    return (
        <div className='add-text-container'>
            <textarea value={value} id="add-text" name="add-text" placeholder="enter text here"
                   onInput={inputActions.onInput}/>
            <button id="add-text-btn" onClick={btnActions.onSubmit}>+</button>
        </div>
    );
}
